.post {
    position: relative;

    font-weight: normal;
}

.post__article {
    width: 676px;
    margin: 50px auto;
    padding: 60px;

    border: solid 20px var(--third-bg-color);
    box-shadow: 5px 5px 10px 0px rgba(0,0,0,0.75);

    background-color: var(--bg-color1);
    color: var(--text-color1);
}

.post__title {
    padding: 20px 0;

    font-size: 170%;
    font-weight: bold;
    color: var(--post-color2);
}

.post__subtitle {
    display: inline-block;

    margin: 0;
    padding: 10px 0;

    font-weight: bold;
    color: var(--post-color2);

    transition: var(--def-trs);
}

.post__img {
    width: 100%;
    padding-bottom: 12px;
}

.post__text {
    margin: 0;
    padding: 0 0 12px;
}

.post__strong {
    text-decoration: underline;
}

.post__col-1 {
    background-color: #00800033;
}

.post__col-2 {
    background-color: #ff000033;
}

.post__text-active {
    position: relative;
}

.post__text-active::after {
    content: ' !!!';
}

.post__list {
    padding-left: 22px;
}

.post__list_item {
    list-style: circle;
}

@media screen and (max-width: 1024px) {
    .post {
        padding-bottom: 10px;
    }

    .post__article {
        padding: 30px;
        border-width: 15px;
        width: 90%;
        max-width: 676px;

        font-size: 20px;

        box-sizing: border-box;
    }

    .post__title {
        padding: 0 0 20px;

        font-size: 150%;
    }

    .post__back {
        position: absolute;
    }
}

@media screen and (max-width: 735px) {
    .post {
        padding-bottom: 0;
    }

    .post__article {
        margin: 0;
        width: 100%;
        padding: var(--pad-txt) var(--pad-txt) 40px;
        border: none;

        font-size: 16px;
    }

    .post__title {
        font-size: 30px;
    }
}
